* {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

body {
  position: relative;
}

input {
  all: unset;
}

button {
  border: none;
  background: none;
  cursor: pointer;
}

ul {
  list-style: none;
}

image {
  width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

:root {
  --bright-red: hsl(12, 88%, 59%);
  --dark-blue: hsl(228, 39%, 23%);
  --dark-grayish-blue: hsl(227, 12%, 61%);
  --very-dark-blue: hsl(233, 12%, 13%);
  --very-pale-red: hsl(13, 100%, 96%);
  --very-light-gray: hsl(0, 0%, 98%);
  --font-family-be-vietnam: "Be Vietnam", sans-serif;
  --font-weight-regular: 400;
  --font-weight-medium: 500;
  --font-weight-bold: 700;
  --padding-layout-mobile: 5vw;
  --padding-layout-tablet: 8vw;
  --padding-layout-desktop: 12vw;
}

html {
  font-size: 16px;
}

body {
  font-family: var(--font-family-be-vietnam);
}

p {
  font-weight: var(--font-weight-regular);
  color: var(--dark-grayish-blue);
  line-height: 1.6;
}

h1, h2, h3, h4 {
  color: var(--dark-blue);
}

h1 {
  font-size: 3.6rem;
}

h2 {
  font-size: 2.5rem;
}

h4 {
  font-family: var(--font-weight-medium);
  font-size: 1.1rem;
}

.list-index {
  background-color: var(--bright-red);
  color: #fff;
  padding: 8px 23px;
  border-radius: 30px;
  margin-right: 1.5rem;
  font-weight: var(--font-weight-bold);
  font-size: 17px;
}

.menu {
  z-index: 1;
  display: none;
  position: absolute;
  top: 5rem;
  left: 0;
  right: 0;
  padding: 3rem;
  margin: 0 var(--padding-layout-mobile);
  border-radius: 5px;
  background-color: var(--very-light-gray);
}
@media (min-width: 60em) {
  .menu {
    display: none;
  }
}
.menu.open {
  display: block;
}
@media (min-width: 60em) {
  .menu.open {
    display: none;
  }
}
.menu__list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.menu__item {
  display: flex;
  place-content: center;
}
.menu__link {
  color: var(--dark-blue);
  font-weight: var(--font-weight-bold);
}

.navbar {
  display: none;
}
@media (min-width: 60em) {
  .navbar {
    display: flex;
    justify-content: center;
  }
}
.navbar__list {
  display: flex;
  gap: 1rem;
}
.navbar__link {
  color: var(--dark-blue);
  font-weight: var(--font-weight-bold);
}

.toggle {
  position: relative;
  width: 25px;
  height: 18px;
  z-index: 1;
}
@media (min-width: 60em) {
  .toggle {
    display: none;
  }
}
.toggle.open {
  height: 20px;
}
.toggle__line {
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: var(--dark-blue);
  transition: all cubic-bezier(0.26, 0.1, 0.27, 1.55) 0.35s;
}
.toggle__line:nth-child(1) {
  top: 0;
}
.toggle__line:nth-child(1).open {
  top: 45%;
  height: 4px;
  transform: rotate(45deg);
}
.toggle__line:nth-child(2) {
  top: 50%;
  opacity: 1;
  transform: translate(0, -50%);
  transition: all 0.3s ease-in-out;
}
.toggle__line:nth-child(2).open {
  opacity: 0;
}
.toggle__line:nth-child(3) {
  bottom: 0;
}
.toggle__line:nth-child(3).open {
  top: 45%;
  height: 4px;
  transform: rotate(-45deg);
}

body {
  position: relative;
}

body.open {
  overflow: hidden;
}

body::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  opacity: 0;
  transition: all 0.3s ease-in-out;
  z-index: -1;
}

body.open::before {
  opacity: 1;
  z-index: 1;
  background-color: rgba(29, 32, 37, 0.1);
}
@media (min-width: 60em) {
  body.open::before {
    display: none;
  }
}

.button-container {
  display: none;
}
@media (min-width: 60em) {
  .button-container {
    display: flex;
    justify-content: end;
  }
}

.button {
  padding: 12px 30px;
  border-radius: 2rem;
  font-weight: var(--font-weight-bold);
  transition: all 350ms;
  box-shadow: 0px 7px 16px #fec7be;
  cursor: pointer;
}
.button__text {
  color: var(--very-light-gray);
}
.button--bright-red {
  background-color: var(--bright-red);
}
.button--bright-red:hover {
  background-color: #f58266;
}
.button--white {
  background-color: #fff;
  color: var(--bright-red);
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.3);
}
.button--white:hover {
  background-color: #fff;
  color: #f9ad9a;
}

.layout {
  display: grid;
  grid-template-columns: var(--padding-layout-mobile) 1fr var(--padding-layout-mobile);
  grid-template-rows: 10rem 1fr auto;
  row-gap: 5rem;
  grid-template-areas: ". header   ." ". content  ." ". footer   .";
}
@media (min-width: 48em) {
  .layout {
    grid-template-columns: var(--padding-layout-tablet) 1fr var(--padding-layout-tablet);
  }
}

.header {
  grid-area: header;
}

.content {
  grid-area: content;
}

.footer {
  grid-area: footer;
}

main {
  max-width: 1440px;
  margin: 0 auto;
  grid-area: main;
}

.header {
  padding: 2rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .logo {
  flex-basis: 20%;
  padding-top: 0.5rem;
  display: flex;
  justify-content: start;
}
.header .navbar {
  flex-basis: 60%;
}
.header .button-container {
  flex-basis: 20%;
}

.hero {
  display: flex;
}
.hero__content {
  flex-basis: 55%;
  display: flex;
  flex-direction: column;
  place-content: center;
}
.hero__image {
  flex-basis: 45%;
}
.hero .button-container {
  justify-content: start;
}

.section-2 {
  width: 100%;
  margin: 10rem 0 8rem;
  display: flex;
  flex-direction: row;
}
.section-2-block {
  max-width: 50%;
}
.section-2-block__heading {
  margin-bottom: 2.2rem;
}
.section-2-block__text {
  max-width: 58%;
}
.section-2-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3.5rem;
  max-width: 50%;
}
.section-2-list__item {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.section-2-list__item h4 {
  margin-bottom: 1.4rem;
}
.section-2-list__item p {
  max-width: 92%;
}

